.footer {
  background: #f7b20e;
  padding: 20px 0 30px;
}

.footer__wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.footer__logo {
  vertical-align: middle;
  margin-right: 25px;
}

.footer__social {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 150px;
}

.footer__icon {
  fill: #f6f6f6;
  transition: 0.3s;

  &:hover,
  &:focus,
  &:active {
    fill: $sec-color;
  }
}

.footer__link {
  color: #f6f6f6;
  text-decoration: none;

  &:hover,
  &:focus,
  &:active {
    color: $sec-color;
  }
}

.footer__contacts {
  text-transform: uppercase;
  color: #f6f6f6;
  font-size: 14px;
  line-height: 16px;

  &:hover,
  &:focus,
  &:active {
    color: $sec-color;
  }
}

.footer__phone {
  font-size: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 170px;
}

@media (max-width: $mobile-width) {
  .footer__social {
    width: 100%;
    justify-content: space-evenly;
    padding: 20px 30px;
    margin: 20px 0;
    border-top: 1px solid #f1aa00;
    border-bottom: 1px solid #f1aa00;
  }

  .footer__logos img {
    max-width: 88px;
  }
}
