.menu {
  flex: 1 1 auto;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0;
}

.menu__item {
  margin: 25px 40px;
  width: 140px;
  position: relative;
}

.menu__link {
  text-transform: uppercase;
  color: $text-color;
  font-size: 14px;
  text-decoration: none;
  position: relative;

  &:hover,
  &:focus,
  &:active {
    color: $prime-color;
  }
}

.popap-menu__link {
  color: $text-color;
  font-size: 11px;
  text-decoration: none;

  &:hover,
  &:focus,
  &:active {
    color: $prime-color;
  }
}

.menu__link--accent {
  position: relative;
  font-weight: bold;

  &::before {
    content: "";
    display: block;
    width: 8px;
    height: 8px;
    position: absolute;
    bottom: -11px;
    left: 0;
    background-color: $prime-color;
  }
}

.popap-menu__link--accent {
  position: relative;
  font-weight: bold;

  &::before {
    content: "";
    display: inline-block;
    width: 8px;
    height: 8px;
    margin-right: 5px;
    background-color: $prime-color;
  }
}

.popap-menu {
  padding: 40px 0 0;
  width: 160px;
  margin: 0;
  list-style: none;
  transition: 0.3s;
  background: $bg-color;
  display: none;

  &.abs {
    display: block;
    height: 0;
    overflow: hidden;
    padding: 0;
    position: absolute;
    top: -15px;
    margin-top: 50px;
    left: 0;
    background: transparent;
    opacity: 0;

    &:hover,
    &:focus,
    &:active {
      opacity: 1;
      height: auto;
      padding: 20px;
      width: 200px;
      background: $bg-color;
      box-shadow: 4px 4px 12px rgba(0, 0, 0, 0.1);
    }
  }
}

.menu__link:hover ~ .popap-menu.abs,
.menu__link:focus ~ .popap-menu.abs,
.menu__link:active ~ .popap-menu.abs  {
  opacity: 0;
  height: auto;
  padding: 20px;
  width: 200px;
  background: $bg-color;
  box-shadow: 4px 4px 12px rgba(0, 0, 0, 0.1);
}

.popap-menu--main {
  text-transform: uppercase;
}

.popap-menu__item:not(:last-of-type) {
  margin-bottom: 25px;
}

#menu__toggle {
  width: 25px;
  height: 25px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  position: absolute;
  right: 0;
  top: 46px;

  &.close {
    background-image: url("../img/menu.svg");
  }

  &.open {
    background-image: url("../img/cross.png");
  }
}

@media (max-width: $mobile-width) {
  #menu__toggle {
    position: static;
    width: 20px;
    height: 20px;
  }
  .menu {
    width: 100%;
    text-align: center;
    margin: 0;
  }

  .menu__link {
    position: relative;
  }

  .menu__item {
    width: 100%;
    padding: 20px;
    font-size: 14px;
    margin: 0;
    border-bottom: 1px solid $sec-color;
    position: relative;

    & .menu__link::after {
      content: "";
      width: 10px;
      height: 6px;
      display: block;
      position: absolute;
      top: 50%;
      margin-top: -3px;
      right: -25px;
      background: url("../img/arr.svg") no-repeat center/contain;
    }

    &--open .popap-menu{
      display: block;
    }

    &--open {
      padding-bottom: 0;
    }
  }

  .menu__link--accent {
    &::before {
      bottom: 5px;
      left: -15px;
    }
  }

  .popap-menu {
    width: 100%;
    padding-top: 20px;
  }

  .popap-menu__item {
    width: 100%;
    padding: 20px;
    font-size: 11px;
    border-top: 1px solid #fff;
    margin-bottom: 0 !important;
  }
}
